/* ==========================================================================
   Cards Gallery shortcode style - begin
   ========================================================================== */

.edgtf-cards-gallery {
	@include edgtfRelativeHolderLayout();
	padding: 10px 0;
	
	.edgtf-cg-inner {
		@include edgtfRelativeHolderLayout();
	}
	
	img {
		position: relative;
		display: block;
		box-shadow: $default-box-shadow;
	}
	
	.edgtf-cg-fake-card {
		position: relative;
		display: block;
		opacity: 0;
		visibility: hidden;
		pointer-events: none;
	}
	
	.edgtf-cg-card {
		position: absolute;
		box-sizing: border-box;
		transition: all .15s ease;
		
		&.edgtf-animating {
			transition: all 1s cubic-bezier(.19, 1, .22, 1);
			pointer-events: none;
		}
		
		&.edgtf-animating-siblings {
			transition: all .6s cubic-bezier(0, 0, 0, 1);
			pointer-events: none;
		}
	}
	
	&.edgtf-cg-shuffled-left .edgtf-cg-card {
		left: 0;
		will-change: transform;
		
		&.edgtf-out {
			transform: translateX(-100%);
		}
		
		&:nth-last-child(6) {
			transform: scale(.5) translateX(80%);
		}
		
		&:nth-last-child(5) {
			transform: scale(.6) translateX(54%);
		}
		
		&:nth-last-child(4) {
			transform: scale(.7) translateX(35%);
		}
		
		&:nth-last-child(3) {
			transform: scale(.8) translateX(21%);
		}
		
		&:nth-last-child(2) {
			transform: scale(.9) translateX(9.2%);
		}
		
		&:not(:last-of-type) {
			cursor: pointer;
		}
		
		&:not(:last-of-type):not(.active):hover {
			margin-left: 10px !important;
		}
	}
	
	&.edgtf-cg-shuffled-right .edgtf-cg-card {
		right: 0;
		will-change: transform;
		
		&.edgtf-out {
			transform: translateX(100%);
		}
		
		&:nth-last-child(6) {
			transform: scale(.5) translateX(-80%);
		}
		
		&:nth-last-child(5) {
			transform: scale(.6) translateX(-54%);
		}
		
		&:nth-last-child(4) {
			transform: scale(.7) translateX(-35%);
		}
		
		&:nth-last-child(3) {
			transform: scale(.8) translateX(-21%);
		}
		
		&:nth-last-child(2) {
			transform: scale(.9) translateX(-9.2%);
		}
		
		&:not(:last-of-type) {
			cursor: pointer;
		}
		
		&:not(:last-of-type):not(.active):hover {
			margin-right: 10px !important;
		}
	}
	
	.no-touch &.edgtf-bundle-animation {
		
		img {
			visibility: hidden;
		}
		
		&.edgtf-cg-shuffled-right.edgtf-appeared {
			@include keyframes(edgtfCardAppearFromRight) {
				0% {
					transform: translate3d(100%, 0, 0);
					opacity: 0;
					visibility: visible;
				}
				
				30% {
					opacity: 1;
					visibility: visible;
				}
				
				100% {
					visibility: visible;
					transform: translate3d(0, 0, 0);
				}
			}
			
			img {
				animation: edgtfCardAppearFromRight 1s cubic-bezier(.19, 1, .22, 1) forwards;
			}
			
			.edgtf-cg-card {
				@for $i from 1 through 8 {
					&:nth-child(#{$i}) {
						img:not(.edgtf-animation-done) {
							animation-delay: $i * .2s;
						}
					}
				}
			}
		}
		
		&.edgtf-cg-shuffled-left.edgtf-appeared {
			@include keyframes(edgtfCardAppearFromLeft) {
				0% {
					visibility: visible;
					opacity: 0;
					transform: translate3d(-100%, 0, 0);
				}
				
				30% {
					visibility: visible;
					opacity: 1;
				}
				
				100% {
					visibility: visible;
					transform: translate3d(0, 0, 0);
				}
			}
			
			img {
				animation: edgtfCardAppearFromLeft 1s cubic-bezier(.19, 1, .22, 1) forwards;
			}
			
			.edgtf-cg-card {
				@for $i from 1 through 8 {
					&:nth-child(#{$i}) {
						img:not(.edgtf-animation-done) {
							animation-delay: $i * .2s;
						}
					}
				}
			}
		}
	}
}

