/* ==========================================================================
   Full Screen Sections shortcode style - begin
   ========================================================================== */

html.fp-enabled,
.fp-enabled body {
	margin: 0;
	padding: 0;
	overflow:hidden;
	
	/*Avoid flicker on slides transitions for mobile phones #336 */
	-webkit-tap-highlight-color: rgba(0,0,0,0);
}

#superContainer {
	height: 100%;
	position: relative;
	
	/* Touch detection for Windows 8 */
	-ms-touch-action: none;
	
	/* IE 11 on Windows Phone 8.1*/
	touch-action: none;
}

.fp-scrollable {
	overflow: hidden;
	position: relative;
}

.fp-scroller {
	overflow: hidden;
}

.iScrollIndicator {
	border: 0 !important;
}

.fp-notransition {
	-webkit-transition: none !important;
	transition: none !important;
}

.fp-auto-height.edgtf-fss-item,
.fp-auto-height .edgtf-fss-slide,
.fp-auto-height .fp-tableCell {
	height: auto !important;
}

.fp-responsive .fp-auto-height-responsive.edgtf-fss-item,
.fp-responsive .fp-auto-height-responsive .edgtf-fss-slide,
.fp-responsive .fp-auto-height-responsive .fp-tableCell {
	height: auto !important;
}

.edgtf-full-screen-sections {
	@include edgtfRelativeHolderLayout();

	&.edgtf-fss-disabled-for-mobile{
		@include ipad-portrait{
			.edgtf-fss-wrapper{
				visibility: visible;
			}
		}
	}
	
	.edgtf-fss-wrapper {
		visibility: hidden;
	}
	
	.edgtf-fss-item {
		@include edgtfTableLayout();
		margin: 0;
		padding: 0;
		background-position: center;
		background-size: cover;
		box-sizing: border-box;
		transition: transform .3s ease;
		
		/*&.active {
			transform: scale(0.92);
			
			&.fp-completely {
				transform: scale(1);
			}
		}
		
		&.fp-completely {
			
			&:not(.active) {
				transform: scale(0.92);
			}
		}*/
		
		.fp-scroller {
			
			> .vc_column_container {
				padding: 0;
				margin: 0;
				
				> .vc_column-inner {
					padding: 0;
					margin: 0;
				}
			}
		}
		
		&.edgtf-fss-item-va-top {
			
			.edgtf-fss-item-inner {
				vertical-align: top;
			}
		}
		
		&.edgtf-fss-item-va-bottom {
			
			.edgtf-fss-item-inner {
				vertical-align: bottom;
			}
		}
		
		&.edgtf-fss-item-ha-left {
			
			.edgtf-fss-item-inner {
				text-align: left;
			}
		}
		
		&.edgtf-fss-item-ha-center {
			
			.edgtf-fss-item-inner {
				text-align: center;
			}
		}
		
		&.edgtf-fss-item-ha-right {
			
			.edgtf-fss-item-inner {
				text-align: right;
			}
		}
		
		&.edgtf-fss-item-has-link {
			
			.edgtf-fss-item-inner {
				
				a {
					position: relative;
					z-index: 2;
				}
			}
		}
		
		.edgtf-fss-item-inner {
			@include edgtfTableCellLayout();
			padding: 5%;
			box-sizing: border-box;
			
			a {
				position: relative;
				z-index: 2;
			}
		}
		
		.edgtf-fss-item-link {
			@include edgtfAbsoluteHolderLayout();
			z-index: 1;
		}
	}
	
	.edgtf-fss-nav-holder {
		position: fixed;
		bottom: 48px;
		left: 57px;
		display: block;
		width: 55px;
		text-align: center;
		visibility: hidden;
		z-index: 1001;
		
		a {
			display: block;
			font-size: 55px;
			line-height: 0.9;
			color: $default-heading-color;
			height: auto;
			opacity: 1;
			transition: color .2s ease-out, opacity .25s ease-out;
			
			&:hover {
				color: rgba($default-heading-color, .7);
			}
			
			.edgtf-light-header & {
				color: #fff;
				
				&:hover {
					color: rgba(#fff, .7);
				}
			}
			
			.edgtf-dark-header & {
				color: #000;
				
				&:hover {
					color: rgba(#000, .7);
				}
			}
			
			span {
				display: block;
				line-height: inherit;
				
				&:before {
					display: block;
					line-height: inherit;
				}
			}
		}
		
		@include ipad-landscape {
			bottom: 28px;
			left: 37px;
		}
	}
}
/* ==========================================================================
   Full Screen Sections shortcode style - end
   ========================================================================== */